import React from 'react'

class Home extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      choose: null,
    }
  }

  row = (index) => {
    let row = [];
    for (let i = 0; i < 20; i++) {
      row.push(<div 
        onClick={this.setState({choose: index + i})} 
        key={index + i} 
        style={{
          width: "30px", height: "30px", border: "1px solid #000000", display: "inline-block", marginLeft:"3px", backgroundColor: ((i + index) == 300) ? "red" : (((i + index) == this.state.choose) ? "green" : "white")}}></div>)
    }
    return row
  }
  
  table = () => {
    let table = [];
    for (let i = 0; i < 20; i++) {
    table.push(<div style={{marginLeft: "30px"}} key={"row" + i}> { this.row( 20*i ) } </div>)
    }
    return table
  }

  render() {
    return (
      <div>
        {
          this.table()
        }
      </div>
    )
  }
}

export default Home